.MainGallery {
  background-color: gray;
  overflow: hidden;
  position: relative;
}

.Swiper {
  display: flex;
  overflow-x: visible;
  transition-property: transform;
  will-change: transform;
}
.MainGallery img {
  object-fit: contain;
}
.imageContainer {
  width: 100%;
  background-color: aqua;
  border: 10px solid #000;
  height: 400px;
}
.move {
  display: flex;
  position: absolute;
  width: 40px;
  height: 40px;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.5);
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: 0;
}

.back {
  left: 5px;
}
.next {
  right: 5px;
}

#skill_cell .titleskillcell {
 
  text-align: left;
  padding:20px 0;
  font-size: 23px;
  font-weight: 500;
}
#skill_cell .titleskillcell Row Col div {
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 20px 0;
}
.skill_img {
  background-color: #fff;
  width: 80px;
  height: 80px;
  object-fit: contain;
}
#skillContainer{
  margin: 20px;
}

/* @MEDIA */
@media only screen and (max-width: 600px) {
  #skill_cell .titleskillcell {
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  #skillContainer{
    justify-content: center;
  align-items: center;;
  }
}