.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #f0f0f0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
}

.App-link {
  border: 0px solid #000;
  padding-left: 0px;
}
.Porto-Center {
  justify-items: center;
  align-items: center;
  width: 100%;
  height: auto;
}
.porto-top {
  background-color: #f8f8f8;
  width: 100%;
  height: auto;
}
.porto-centerpart {
  background-color: #18bc9c;
  width: 100%;
  height: auto;
  padding-bottom: 100px;
}
.porto-centerbottom {
  color: #fff;
  background-color: #45b4ff;
  width: 100%;
  height: auto;
}
.porto-footer {
  background-color: #2c3e50;
  color: #fff;
  width: 100%;
  height: auto;
  padding-bottom: 50px;
}
.Porto-copyright {
  background-color: #233140;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  height: auto;
  color: #fff;
  font-size: 13px;
}
.bannerSize {
  width: "100%";
  height: 400px;
  object-fit: cover;
}
.bannerPortoSize {
  width: "100%";
  height: 400px;
  object-fit: cover;
  object-position: 0 0;
}
.CellPorto {
  position: relative;
  width: 400px;
  height: 300px;
  background-color: #f2f3f5;
  overflow: hidden;
  margin: 5px 15px;
  display: flex;
  justify-content: center;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.CenterText {
  display: block;
  flex: auto;
  text-align: center;
}
.TextTitleWelcome {
  color: #fff;
  font-size: 40px;
  font-weight: bold;
}
.TextTitleWelcomeDes {
  color: #fff;
  font-size: 26px;
  font-weight: 400;
  margin: auto 17%;
}
.TextTitlePorto {
  font-size: 30px;
  color:#18bc9c;
  text-shadow: 3px 3px rgba(0,0,0,0.2);
  font-weight: bold;
}
.TextTitleAbout {
  font-size: 30px;
  color:#1fa4ff;
  text-shadow: 3px 3px rgba(0,0,0,0.2);
  font-weight: bold;
}
.TextTitleContainer {
  padding: 5px 10px 10px 10px;
  background-color:rgba(222, 222,222, 0.4);
  opacity: 0.5px;
  border-bottom-right-radius: 30px;
  width: 250px;
}
.TextSubTitle {
  color: #2c3e50;
  font-size: 27px;
  font-weight: bold;
}
.ContactContainer{
  max-width: 1000px;
  flex:1;
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
  justify-content: center;
}
.ContactItem{
  box-sizing: border-box;
  padding:10px;
  flex:1;
}
.TextContact {
  color: #FFF;
  
  font-size: 27px;
  
  font-weight: bold;
}
.TextContactDesc {
  color: #FFF;
  font-size: 23px;
  font-weight: 500;
}
.BorderWelcome {
  border: 2px solid #fff;
  width: 300px;
  float: none;
  margin: auto;
}
.BorderTitle {
  border: 1px solid;
  width: 85%;
}
.PortotitleContainer{
  position: absolute;
  width: auto;
  left: 0;
  bottom:0;
}
.PortotitleContainer span{
  color: #f8f8f8;
  font: bold 24px/45px Helvetica,Sans-Serif;
  background:rgb(0,0,0);
  background: rgba(255,255,255,0.2);
  border-top-right-radius: 15px;
  padding: 10px;
}
.PortoHover{
  display: flex;
  position: absolute;
  background: rgba(24, 188, 156,0.8);
  justify-content: center;
  align-items: center;
  color:#fff;
  top:0;
  right:0;
  bottom: 0;
  left: 0;;
}


#about p{
  font-size: 21px;
  font-weight:500;
  text-align: justify;
  word-break: break-all;
  margin: 0 10px;
  font-family: Arial, Helvetica, sans-serif;
  padding-bottom: 20px;
}
#about{
  padding-bottom: 50px;
}
#MorePorto h3{
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  color: #000;
  
  text-decoration: none;
}
#MorePorto :hover{
  color: #18bc9c;
  text-decoration: none;
}
#MorePorto a{
  text-decoration: none;
}
