.header {
  text-align: center;
  font-family: Helvetica;
}
/* MOBILE VOEW */
@media only screen and (max-width: 1000px) and (min-width: 600px) {
  .CellPorto {
    margin: 30px;
    height: 260px;
  }
}
@media only screen and (max-width: 600px) {
  .CellPorto {
    margin: 30px;
    height: 200px;
  }
  .ContactItem{
    flex:none;
    padding:20px;
  }
  .TextTitleWelcomeDes{
    margin:auto 5%;
    font-size: 21px;
  }
}
